@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css');

/* common */

* { font-family: Pretendard; color: #444; }
img { width: 100%; height: 100%; object-fit: cover; }
a { text-decoration: none; }
ul, p { margin: 0; padding: 0; }

.App { text-align: center; }
.cm_inner { margin: 70px 0;  }
.cm_title01 { margin-bottom: 50px;  }

.fade { transform: scale(0); }
.show { transform: scale(1); transition: transform 0.5s;}

/***************
* main section
****************/
/* sec01 */
.main_bg {
    height: 400px;
    background: url('../public/img/bg1.jpg') 50% 100%;
    background-size: cover;
    background-attachment: fixed;
}

/* sec02 */
.best_shop .shop_list { margin-bottom: 20px; }
.best_shop .shop_list .title { font-size: 18px; }
.best_shop .shop_list .shop_img{
    margin-bottom: 20px;
    height: 400px;
    overflow: hidden;
}
.best_shop .shop_list .shop_img img { transform: scale(1,1); transition:0.5s; }
.best_shop .shop_list .shop_img:hover img { transform: scale(1.1,1.1); }

/***************
* datail section
****************/
.datail { margin-top: 15px; }
.datail .shop_img { height: 700px; }
.datail .tab_wrap a { color: #757575; }
.datail .tab_cont > div { padding: 30px; }
